  text-align: center;
  padding-top: 25px;
  /* transform: translate(0%, -50%); */
  /* background-color: #DB4437; */
}

.l {
  color: black;
  opacity: 0;
  animation: op 2s ease-in-out infinite;
  animation-delay: 0.2s;
}

.o {
  color: black;
  opacity: 0;
  animation: op 2s ease-in-out infinite;
